<template>
    <div class="field" v-bind:class="{ required: props.option && props.option.required }">
        <label>{{ props.label }}</label>
        <div class="inline fields">
            <div class="field" v-for="n in numbers">
                <div class="ui radio checkbox">
                    <input type="radio" :name="'field'+props.sn" :id="'field'+props.sn+n" class="hidden" :value="n">
                    <label :for="'field'+props.sn+n">{{ n }}</label>
                </div>
            </div>
        </div>
        <p class="postscript" v-if="props.postscript">{{ props.postscript }}</p>
    </div>
</template>

<script>
    import axios from 'axios'

    export default {
        name: 'RadioNumber',
        props: [
            'props'
        ],
        data: function () {
            return {
                numbers: []
            }
        },
        computed: {
            rangeNumbers: function () {
                return this.numbers.filter(function (number) {
                return number % 2 === 0
            })
            }
        },
        mounted() {
            const self = this;

            for(let i = parseInt(self.props.option.start); i <= parseInt(self.props.option.end); i++) {
                self.numbers.push(i);
            }

            $('.ui.radio.checkbox').checkbox();
        }
    }
</script>